.gig-details-page {
  margin-top: 18px;
  // align-items: baseline;
  // align-items: baseline;
  // height: 300vh;


  // align-items: flex-start;
  // height: 340vh;

  &.no-user {
    margin-top: 98px;
  }

  .about-gig {
    // color: #62646a;
    color: #404145;
    // display: block;
    padding: auto;
    width: 100%;
    font-size: 22px;
    font-weight: 400;
    outline-color: rgb(98, 100, 106);

    @include for-order-normal-layout {
      max-width: 712px;
    }

    p {
      font-stretch: 100%;
      line-height: 28px;
      letter-spacing: 0.8px;
      font-size: 18px;
      color: #62646a;
    }
  }
}


.gig-details-container {
  padding: 0px 70px 0;
  width: 100%;

  // display: flex;
  // flex-direction: column;
  grid-column: 1/ -1;

  @include for-order-normal-layout {
    grid-column: 1;
    // width: 70%;
  }

  .details-header {
    margin-bottom: 16px;

    .gig-title {
      color: #404145;
      font-size: 28px;
      overflow-wrap: break-word;
      line-height: 130%;
      letter-spacing: 0.6px;
      font-weight: 700;
      padding-bottom: 16px;
    }

    .stars {
      display: flex;
      align-items: center;
      // margin-inline: 5px;
      // font-size: 40px;

      .star {
        color: #ffb33e;
        // padding: 1px;
        font-size: 21px;
        line-height: 21px;
      }

      .rate {
        color: #ffb33e;
        font-weight: bold; // text-align: center;
        // align-items: center;
        // vertical-align: text-bottom;
        font-size: 16px;
        font-weight: 700;
        // padding-bottom: 15px;
        margin-left: 4px;
        // line-height: 21px;

      }
    }

    .owner-info {
      display: flex;
      flex-direction: row;
      align-items: center;
      gap: 10px;

      .owner-name {
        color: #404145;
        font-size: 16px;
      }

      .owner-level {
        font-size: 15px;
        color: #95979d;

        &.top {
          color: #ffb33e;
        }
      }

      .spacer {
        font-size: 12px;
        color: #c5c6c9;
        // margin-right: 8px;
      }

      .user-img {
        background-size: cover;
        background-repeat: no-repeat;
        height: 30px;
        width: 30px;
        border-radius: 50%;
      }
    }
  }

  .gig-img {
    display: flex;
    flex-direction: column;

    .carousel {
      margin-bottom: 20px;
      overflow: visible;
      max-width: 808px;


      .inner-wrapper {
        // position: relative;
        // max-width: 712px;

        overflow: hidden;


        .inner {
          // max-width: 712px;
          height: 460px;
          // height: 389px;
          // max-width: 800px;
          padding: 0 auto;

          // @include for-order-normal-layout {
          //   max-width: 645px;
          //   padding: 0 auto;
          // }

          // @include for-order-wide-layout {
          //   width: 645px;
          // }


          .carousel-item {
            display: inline-flex;
            align-items: center;
            justify-content: center;
            width: 100%;
            height: 100%;
            object-fit: cover;
            background-position: center;
            background-size: contain;
            background-repeat: no-repeat;
            color: #fff;
            background-color: $clr4;
            transition: 0.5s;
          }
        }
      }

      .carousel-btn-container {
        display: flex;
        align-items: center;
        justify-content: center;
        padding-block: 10px;
        // height: 65px;
      }

      button {
        height: 70px;
        width: 100px;
        background-size: cover;
        opacity: 0.6;
        border: none;
        transition: opacity 0.3s;
      }

      button.active {
        opacity: 1;
      }

      .indicators {
        display: block;
        top: 52%;
        color: #404145;


        &> :first-child {
          position: absolute;
          left: -20px;
        }

        &> :last-child {
          position: absolute;
          right: -20px;
        }

        justify-content: space-between;
        transform: translateX(-50%);

        // opacity: 0;
        span {
          transform: translateY(-50%);
          transition: 0.4s all;
          bottom: 0px;
        }

        &.show {

          // opacity: 1;
          &> :first-child {
            left: 0;
          }

          &> :last-child {
            right: 0;
          }
        }
      }

      .indicators>span {
        background-color: white;
        color: $clr3;
        border: .1px solid darken(white, 1%);

        svg {
          font-size: 13px;
          position: relative;
          color: #404145;
          font-weight: 700;
        }

        &:first-child {
          padding: 5px 5px 8px 9px;
          border-end-end-radius: 50%;
          border-top-right-radius: 50%;
        }

        &:first-child svg {
          top: 2px;
          bottom: 2px;
          right: 3px;
        }

        &:last-child {
          padding: 5px 8px 9px 5px;
          border-top-left-radius: 50%;
          border-bottom-left-radius: 50%;
        }

        &:last-child svg {
          inset: 2px 3px;
        }
      }

      .indicators>span {
        position: absolute;
        cursor: pointer;
        background-color: #ffffff;
        color: rgb(63, 62, 62);
        height: 55px;
        width: 55px;
        border-radius: 50%;
        display: flex;
        justify-content: center;
        justify-self: center;
        align-items: center;
        box-shadow: 0px 0px 3px 0px #0000001e;
        // box-shadow: 0px 0px 5px 2px  rgb(63, 62, 62);

        svg {
          font-size: 18px;
          font-weight: 1000;
        }

        // &:first-child {
        // margin-inline-start: 5px;
        // }

        // &:last-child {
        // margin-inline-end: 5px;
        // }
      }
    }
  }

  .about-seller {
    max-width: 712px;
  }
}


