.filters-div {
  align-items: flex-end;
  flex-direction: row;
  margin-bottom: 15px;

  @media (max-width: 450px) {
    justify-content: space-between;
  }

  .budget-div {
    position: relative;

    font-family: work-sans;
    font-style: normal;
    color: #222325;

    //   margin: 0 4px;
    //   height: 40px;
    .budget-select {
      cursor: pointer;
      line-height: 1.4375em;
      height: 35px;
      border-radius: 4px;
      color: #222325;
      // font-family: arial;
      // font-style: normal;
      border: 1px solid #c4c4c4;
      padding: 5px 10px 8px 15px;
      width: 120px;
      display: flex;
      align-items: center;

      &:hover {
        border: 1px solid #222325;
      }

      transition: border-color 0.12s cubic-bezier(0.22, 0.61, 0.36, 1);

      span {
        &.arrow {
          color: gray;
          transform: rotate(0deg);
          display: flex;
          align-items: center;
          transition: all 0.24s cubic-bezier(0.215, 0.61, 0.355, 1);

          &.open {
            transform: rotate(180deg);
          }
        }
      }
    }

    .budget-content {
      position: absolute;
      z-index: 100;
      opacity: 0;
      pointer-events: none;
      transition: all 0.24s cubic-bezier(0.215, 0.61, 0.355, 1),
        opacity 0.12s cubic-bezier(0.215, 0.61, 0.355, 1);

      &.open {
        opacity: 1;
        pointer-events: all;
      }

      border: 1px solid $clr6;
      line-height: 22px;
      font-size: 14px;
      top: 100%;
      margin-top: 10px;
      border-radius: 4px;
      background-color: #fff;
      color: $clr5;
      box-shadow: 0 12px 18px 0.2;

      .budget-filter {
        padding: 12px 24px;
        transition: all 0.24s cubic-bezier(0.215, 0.61, 0.355, 1);
      }

      .price-filter {
        gap: 10px;

        .input-wrapper {
          margin-bottom: 12px;

          label {
            margin-bottom: 4px;
            font-size: 1rem;
            line-height: 24px;
            color: $clr5;
            font-weight: 600;
          }

          input {
            height: 40px;
            width: 120px;
            border: 1px solid #c5c6c9;
            color: $clr2;
            padding: 8px 12px;

            &:focus {
              outline: none;
            }
          }
        }
      }

      .budget-btns {
        padding: 16px 24px;
        justify-content: space-between;

        .close-btn {
          border: 0;
          color: #95979d;
          font-size: 1rem;
          background: inherit;
        }

        .btn {
          height: 30px;
          display: flex;
          align-items: center;
        }
      }
    }
  }
}
}

.filters {
  display: flex;
  justify-content: space-between;
  width: 100%;

  @media (max-width: 450px) {
    flex-direction: column;
    gap: 10px;
  }

  button {
    &.clear-btn {
      padding: 0;
      white-space: nowrap;
      display: flex;
      justify-content: flex-end;
      background-color: inherit;
      border: 0;
      color: #222325;
      font-size: 1rem;
      font-weight: 700;
      //   font-family: Arial, Helvetica, sans-serif;
      display: flex;
      align-items: center;
    }

    &.applay-filters {
      display: none;
    }
  }
}

