// fiver gig-list/preview
// wide  4 326x383 - pic 324x194
// b1160 3 348x395 - pic 348x209
// b888  2 402x428 - pic 402x241
// b600  1 547x515 - pic 547x328
.gig-preview {
  color: $clr2;
  padding: 0px;
  min-width: 245px;
  text-align: center;
  transition:0.15s;
  border-radius: .2em;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  background-color: #FFFFFF;
  border: 1px solid #EFEFF0;
  margin-bottom: 21px;
  @include for-gig-list-wide-layout{
    max-width: 325px;

  }


  &:hover {
    .gig-title {
      color: $clr1;
    }
  }

  .gig-img {
    width: 100%;
    // height: 150px;
    cursor: pointer;

    img {
      width: 100%;
      // height: 100%;
      object-fit: cover;
    }
  }

  .owner-info {
    display: flex;
    flex-direction: row;
    align-items: center;
    gap: 10px;
    padding: 8px 0 8px 12px;

    .owner-name-level {
      display: flex;
      flex-direction: column;

      h5 {
        margin: 0;
      }

      .owner-name {
        color: #222325;
        font-weight: 600;
        // font-family: work-sans, sans-serif, serif;
      }

      .owner-level {
        // font-family: work-sans, sans-serif, serif;
        font-weight: 400;
        font-size: 14px;
        line-height: 1.5;
        color: #74767E;
        text-align: left;

      }

    }

    .user-img {
      background-size: cover;
      background-repeat: no-repeat;
      height: 20px;
      width: 20px;
      border-radius: 50%; 
    }

    .owner-pic {
      background-size: cover;
      background-repeat: no-repeat;
      height: 30px;
      width: 30px;
      border-radius: 50%;
    }
  }

  .gig-title {
    margin: 0;
    // font-size: 16px;
    font-family: work-sans;
    text-align: left;
    color: #222325;
    min-height: 40px;
    // padding: 8px 20px 8px 12px;
    padding: 0px 12px 5px;
    // margin: 5px 0 0;
    cursor: pointer;
    transition: 0.3s all;
    // height: 3.2em;
    overflow: hidden;
  }

  .rate-wrapper {
    padding: 15px 15px;
    font-size: 1rem;

    .gig-rating {
      display: flex;
      align-items: center;
      height: 18px;
      // text-transform:none;
      color: #FFBE5B;
      font-weight: 700;
      font-size: 1rem;
      span {
        font-size: 12px;

      }

    }

    .review-number {
      color: #B5B6BA;
      font-weight: 400;
      margin-inline-start: 2px;
    }

    .star {
      width: 18px;
      height: 18px;
      margin-right: 5px;
      vertical-align: -0.16em;
      font-size: 1rem;
    }
  }

  .card-footer {
    border-top: 1px solid #EFEFF0;
    // position: relative;
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 0px 8px 0px 8px;

    p {
      font-size: 11px;
      color: #74767E;

      span {
        font-size: 16px;
        color: #404145;
        padding-left: 6px;
      }
    }

    .like {
      color: lighten($clr3, 20%);
      transition: color 0.3s;
      display: flex;

      svg {
        height: 20px;
      }

      &.active {
        color: rgba(218, 95, 95, 0.829);
      }

      &:hover {
        cursor: pointer;
      }
    }





    //   ul {
    //     cursor: pointer;
    //   }

    //   & ul li:first-child {
    //     text-transform: uppercase;
    //     color: #7a7d85;
    //     font-size: 10px;
    //     font-weight: 700;
    //     letter-spacing: 0.5px;
    //     padding: 2px 0;
    //   }

    //   & ul li:not(:first-child) {
    //     margin-top: 2px;
    //     font-size: 18px;
    //     line-height: 20px;
    //     color: $clr2;
    //     text-align: right;
    //   }
  }

  .carousel-btn-container {
    justify-content: center;
    position: absolute;
    left: 50%;
    margin-bottom: 5px;
    transform: translate(-50%, -50%);
    width: 100%;
    display: flex;
    align-items: center;

    button {
      border-radius: 50%;
      height: 5px;
      width: 5px;
      padding: 0 0px;
      border: none;
      background-color: rgb(241, 238, 238);
      transition: transform 0.3s;
    }

    button.active {
      transform: scale(1.5);
    }
  }

  .carousel-item {
    display: inline-flex;
    align-items: center;
    justify-content: center;
    height: 197px;
    background-size: cover;
    color: #fff;
  }

  .indicators {
    display: block;
    top: 56%;
  
    &> :first-child {
      position: absolute;
      left: -26px;
    }
  
    &> :last-child {
      position: absolute;
      right: -26px;
    }
  
    justify-content: space-between;
    transform: translateX(-50%);
  
    // opacity: 0;
    span {
      transform: translateY(-50%);
      transition: 0.4s all;
    }
  
    &.show {
  
      // opacity: 1;
      &> :first-child {
        left: 0;
      }
  
      &> :last-child {
        right: 0;
      }
    }
  }
  
  .indicators>span {
    background-color: white;
    color: $clr3;
    border: 1px solid darken($clr4, 15%);
  
    svg {
      font-size: 14px;
      position: relative;
    }
  
    &:first-child {
      padding: 5px 5px 5px 5px;
      border-end-end-radius: 50%;
      border-top-right-radius: 50%;
    }
  
    &:first-child svg {
      top: 2px;
      bottom: 2px;
      right: 3px;
    }
  
    &:last-child {
      padding: 5px 5px 5px 5px;
      border-top-left-radius: 50%;
      border-bottom-left-radius: 50%;
    }
  
    &:last-child svg {
      inset: 2px 3px;
    }
  }
  
}





.popup .popuptext {
  visibility: hidden;
  width: 130px;
  padding: 8px;
  border-radius: 0.5em;
  background-color: #555;
  color: #fff;
  text-align: center;
  position: absolute;
  z-index: 1;
  bottom: 125%;
  // left: 50%;
  margin-left: -53px;
}

/* Popup */
.popup .popuptext::after {
  content: "";
  position: absolute;
  top: 100%;
  left: 50%;
  margin-left: -5px;
  border-width: 5px;
  border-style: solid;
  border-color: #555 transparent transparent transparent;
}

.popup {
  position: relative;
  display: inline-block;
  cursor: pointer;
  // -webkit-user-select: none;
  // -moz-user-select: none;
  // -ms-user-select: none;
  user-select: none;
}

.popup .show {
  visibility: visible;
  -webkit-animation: fadeIn 1.2s;
  animation: fadeIn 1.2s;
}